@use './mixins/fluid' as *;
@use 'functions' as *;

@use "vars" as *;
@use "mixins" as *;
@use "fonts" as *;
@use "settings" as *;

.all_pages {
  position: fixed;
  top: 20%;
  right: 0;
  z-index: 1000;
  &:hover {
    ul {
      right: 0;
    }
  }
  .page_point {
    padding: 10px 15px;
    background: #00d2ff;
    cursor: pointer;
    color: #fff;
  }
  ul {
    background: #00d2ff;
    padding: 15px 20px;
    position: absolute;
    top: 100%;
    width: 200px;
    right: -200px;
    transition: 0.4s;
    max-height: 450px;
    overflow-y: auto;
    li {
      list-style: none;
      margin-bottom: 15px;
      font-size: 16px;
      a {
        color: #fff;
      }
    }
  }
}

// Header start ========================================================================================================================================================

.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 105;
  width: 100%;
  &.scrolling {
    .header__promo {
      padding-block: 10px;
    }
    .header__inner {
      padding-block: 10px;
    }
    .header__wrap {
      border-bottom: 2px solid rgba(#FFD11A, 0.2);
    }
  }

  // .header__promo

  &__promo {
    padding-block: 20px;
    background-color: var(--color-dark);
    transition: padding-block 0.3s ease 0s;
    @include tablet {
      padding-block: 10px;
    }

    a {
      font-style: normal;
      font-weight: 400;
      line-height: 1.5;
      letter-spacing: -0.54px;
    }
  }

  // .header__wrap

  &__wrap {
    border-top: var(--border);
    border-bottom: var(--border);
    background-color: var(--color-dark-10);
  }

  // .header__inner

  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-block: 24px;
    transition: padding-block 0.3s ease 0s;
    @include tablet {
      padding-block: 10px;
    }
    nav {
      position: relative;
      > ul {
        display: flex;
        column-gap: 16px;
        > li {
          > a {
            padding: 14px 24px;
            &.is-active {
              color: var(--color-light);
              font-weight: 500;
              border-radius: 10px;
              border: 1px solid var(--color-dark-20);
              background-color: var(--color-dark);
            }
            @include hover {
              color: var(--color-light);
            }
          }
        }
      }
      @include tablet {
        position: fixed;
        inset: 0;
        width: 100%;
        height: 100vh;
        overflow-y: auto;
        z-index: 99;
        background-color: var(--color-dark);
        transform: translateX(100%);
        transition: all 0.3s ease 0s;
        &.menu--active {
          transform: translateX(0);
        }
        > ul {
          width: 100%;
          height: 100%;
          flex-direction: column;
          justify-content: center;
          row-gap: 15px;
          align-items: center;
        }
      }
    }
  }

  // .header__navigation

  &__navigation {
    display: contents;
    @include tablet {
      display: block;
    }
  }

  // .header__logo

  &__logo {
    position: relative;
    z-index: 100;
    @include laptop {
      img {
        width: 144px;
        height: 40px;
      }
    }
    @include mobile-s {
      img {
        width: 125px;
        height: 35px;
      }
    }
  }

  // .header__btn

  &__btn {
    padding: 14px 20px;
    @include tablet {
      position: absolute;
      left: 50%;
      bottom: 5%;
      transform: translateX(-50%);
      padding: 10px 20px;
    }
  }
}

.burger {
  @include burger;
    display: none;
  @include tablet {
    display: block;
    z-index: 100;
  }
}

// Header end ========================================================================================================================================================

// Section hero start ========================================================================================================================================================

.hero {
  margin-top: var(--header-height);

  @include mobile {
    > .container {
      padding-inline: 0;
    }
  }

  // .hero__inner

  &__inner {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: auto;
    @include mobile {
      display: block;
    }
  }

  // .hero__body

  &__body {
    grid-column: 1 / 4;
    padding: 150px 40px 100px 0;
    border-bottom: var(--border);
    @include laptop {
      padding: 100px 40px 100px 0;
    }
    @include mobile {
      padding: 40px 16px;
    }
  }

  // .hero__subtitle

  &__subtitle {
    @include fluid-text(30, 18);

    color: var(--color-dark-40);
    font-family: "KumbhSans";
    font-style: normal;
    font-weight: 500;
    line-height: 120%; /* 36px */
    letter-spacing: -0.9px;
    margin-bottom: 30px;
  }

  // .hero__title

  &__title {
    margin-bottom: 20px;
  }

  // .hero__text

  &__text {
    max-width: 820px;
  }

  // .hero__metrics

  &__metrics {
    grid-column: 1 / 4;
  }

  // .hero__resources

  &__resources {
    grid-column: 4 / 6;
    grid-row: 1 / 3;
    padding-left: 80px;
    padding-bottom: 80px;
    border-left: var(--border);
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
    background-repeat: no-repeat;
    background-size: 653px 755px;
    background-position: 0 0;
    @include laptop {
      padding-left: 50px;
      padding-bottom: 50px;
    }
    @include tablet {
      padding-left: 30px;
      padding-bottom: 30px;
    }
    @include mobile {
      padding: 100px 40px 40px 40px;
      background-image: url('../img/bg_tablet.webp') !important;
      background-size: 300px auto;
    }
  }

  .resources-hero {

    // .resources__team

    &__team {
      margin-bottom: 30px;
    }

    // .resources__title

    &__title {
      @include fluid-text(24, 18);

      color: var(--color-light);
      font-style: normal;
      font-weight: 500;
      line-height: 1.5;
      letter-spacing: -0.72px;
      margin-bottom: 14px;
    }

    // .resources__subtitle

    &__subtitle {
      @include fluid-text(18, 14);

      color: var(--color-gray-60);
      font-style: normal;
      font-weight: 400;
      line-height: 1.5;
      letter-spacing: -0.54px;
      margin-bottom: 30px;
    }

    // .resources__btn

    &__btn {
      padding: 20px 24px;
      @include mobile {
        padding: 14px 20px;
      }
      @include mobile-s {
        width: 100%;
      }
    }
  }

  .feature-hero {
    position: relative;
    border-bottom: var(--border);
    border-top: var(--border);
    > .container {
      padding: 0;
    }

    // .feature-hero__list

    &__list {
      display: flex;
      @include mobile {
        flex-direction: column;
      }
    }

    // .feature-hero__item

    &__item {
      flex-basis: calc(100% / 3);
      &:not(:first-child) {
        border-left: var(--border);
        @include mobile {
          border-left: 0;
          border-top: var(--border);
        }
      }
      padding: 50px 80px;
      &:first-child {
        padding-left: 16px;
        @include mobile {
          padding-left: 16px;
        }
      }
      &:last-child {
        padding-right: 16px;
        @include mobile {
          padding-right: 16px;
        }
      }
      @include laptop {
        padding: 40px 50px;
      }
      @include tablet {
        padding: 25px;
      }
      @include mobile {
        padding: 30px 16px;
      }
    }

    // .feature-hero__icon

    &__icon {
      --iconItemSize: 50px;
      width: var(--iconItemSize);
      height: var(--iconItemSize);
      margin-bottom: fluid(30, 16);
      > img {
        width: 100%;
        height: 100%;
        display: block;
      }
      @include laptop {
        --iconItemSize: 40px;
      }
      @include mobile-m {
        --iconItemSize: 30px;
      }
    }

    // .feature-hero__link

    &__link {
      display: block;
      width: 100%;
      position: relative;
      display: flex;
      flex-direction: column;
      margin-bottom: 30px;
      &:after {
        content: '';

        @include square(52px);

        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
        border-radius: 50%;
        background: url('../img/icon-arrow-top-dark.svg') 50% 50%/24px 24px no-repeat;
        background-color: var(--color-accent);
        @include laptop {
          @include square(44px);
          background-size: 20px 20px;
        }
        @include mobile-m {
          @include square(40px);
        }
      }
      @include hover {
        .feature-hero__title {
          color: var(--color-accent);
        }
      }
    }

    // .feature-hero__title

    &__title {
      @include fluid-text(20, 16);

      color: var(--color-light);
      font-style: normal;
      font-weight: 500;
      line-height: 1.5;
      letter-spacing: -0.6px;
      margin-bottom: 4px;
      transition: color var(--transition-duration);
    }

    // .feature-hero__subtitle

    &__subtitle {
      @include fluid-text(18,14);

      color: var(--color-dark-50);
      font-style: normal;
      font-weight: 400;
      line-height: 1.5;
      letter-spacing: -0.54px;
    }

    // .feature-hero__descr

    &__descr {
      @include fluid-text(20, 16);
      color: var(--color-gray-60);
      font-family: "KumbhSans";
      font-style: normal;
      font-weight: 400;
      line-height: 1.5;
      letter-spacing: -0.6px;
    }
  }
}

.metrics {
  position: relative;

  // .metrics__list

  &__list {
    display: flex;
    align-items: stretch;
    @include mobile {
      border-bottom: var(--border);
    }
  }

  // .metrics__item

  &__item {
    flex-basis: calc(100% / 3);
    padding: 50px;
    &:first-child {
      padding-left: 0;
      @include mobile {
        padding-left: 16px;
      }
    }
    display: flex;
    flex-direction: column-reverse;
    justify-content: flex-end;
    height: auto;
    &:not(:first-child) {
      border-left: var(--border);
    }
    @include laptop {
      padding: fluid(30, 20) fluid(40, 20);
    }
  }

  // .metrics__count

  &__count {
    @include fluid-text(40, 24);
    color: var(--color-light);
    font-style: normal;
    font-weight: 600;
    line-height: 1.5;
    letter-spacing: -1.2px;
    margin-bottom: 10px;
    span {
      color: var(--color-accent);
    }
  }

  // .metrics__descr

  &__descr {
    @include fluid-text(18, 14);

    color: var(--color-gray-60);
    font-style: normal;
    font-weight: 400;
    line-height: 1.5;
    letter-spacing: -0.54px;
  }
}

.team {
  border-radius: 100px;
  border: 1px solid var(--color-dark-15);
  background: var(--color-dark-10);
  overflow: hidden;
  padding: 10px;
  max-width: max-content;

  // .team__list

  &__list {
    display: flex;
  }

  // .team__item

  &__item {
    --personItemSize: 60px;
    width: var(--personItemSize);
    height: var(--personItemSize);

    overflow: hidden;
    &:not(:first-child) {
      margin-left: calc((var(--personItemSize) / 4) * -1);
    }

    img {
      display: block;
      border-radius: 42px;
      width: 100%;
      height: 100%;
      border: 2px solid var(--color-dark-40);
    }
    @include laptop {
      --personItemSize: 50px;
    }
    @include mobile-m {
      --personItemSize: 40px;
    }
  }
}

// Section hero end ========================================================================================================================================================

// Section feature start ========================================================================================================================================================

.feature {

  // .feature__subtitle

  &__subtitle {
    margin-bottom: 10px;
  }

  // .feature__box

  &__box {
    border-bottom: var(--border);
    @include tablet {
      border-bottom: 0;
      &:last-child {
        .feature__content {
          border-bottom: 0;
        }
      }
    }
  }

  // .feature__wrapper

  &__wrapper {
    display: flex;
    @include tablet {
      flex-direction: column;
    }
  }

  // .feature__descr

  &__descr {
    flex-basis: 40%;
    padding: fluid(80, 30) fluid(80, 30) fluid(80, 30) fluid(0, 0);
    border-right: var(--border);
    display: flex;
    flex-direction: column;
    justify-content: center;
    @include tablet {
      border-right: 0;
      border-bottom: var(--border);
      align-items: center;
      padding: 50px 0;
    }
  }

  // .feature__icon

  &__icon {
    margin-bottom: fluid(50, 20);
  }

  // .feature__name

  &__name {
    margin-bottom: 16px;
  }

  // .feature__text

  &__text {
    max-width: 540px;
    @include tablet {
      text-align: center;
    }
  }

  // .feature__content

  &__content {
    flex-basis: 60%;
    flex-grow: 1;
    padding: fluid(80, 30) fluid(0, 0) fluid(80, 30) fluid(80, 30);
    @include tablet {
      border-bottom: var(--border);
      padding: fluid(60, 30) fluid(60, 16);
    }
  }

  // .feature__list

  &__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: auto;
    gap: fluid(30, 10);
    @include mobile {
      grid-template-columns: 1fr;
    }
  }

  // .feature__item

  &__item {
    border-radius: 12px;
    border: var(--border);
    background-color: var(--color-dark-10);
    padding: fluid(40, 24);
    transition: border 0.3s ease 0s;
    @include hover {
      border-color: rgba(#FFD11A, 0.5);
    }
  }
}

// Section feature end ========================================================================================================================================================

// Section blog start ========================================================================================================================================================

.blog {

  // .blog__subtitle

  &__subtitle {
    margin-bottom: 10px;
  }

  // .blog__btn

  &__btn {
    padding: fluid(18, 14) fluid(24, 20);
  }
}

.tabs {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  position: relative;
  column-gap: fluid(20, 14);
  padding-block: fluid(50, 20);
  border-bottom: var(--border);

  @include laptop {
    overflow-x: auto;
  }
}

.tab-links{
  padding-block: fluid(30, 18);
  min-width: fluid(250, 150);
  &.active {
    background-color: var(--color-dark-10);
    border-color: #FFD11A;
    pointer-events: none;
  }
}

.card {

  // .card__item

  &__item {
    position: relative;
    &:not(:last-child) {
      border-bottom: var(--border);
    }
    padding-block: fluid(80, 40);
    display: flex;
    align-items: center;
    column-gap: fluid(50, 20);
    @include tablet {
      justify-content: space-between;
      align-items: flex-start;
      flex-wrap: wrap;
      row-gap: 30px;
    }
  }

  // .card__user

  &__user {
    flex-basis: 24%;
    display: flex;
    align-items: center;
    column-gap: fluid(16, 10);
    align-self: flex-start;
    @include tablet {
      flex-basis: auto;
    }
  }

  // .card__icon

  &__icon {
    --iconSize: 80px;
    @include square(var(--iconSize));

    flex-shrink: 0;
    align-self: flex-start;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    @include mobile-s {
      --iconSize: 60px;
    }
  }

  // .card__info

  &__info {
    display: flex;
    flex-direction: column;
    row-gap: 2px;
  }

  // .card__name

  &__name {
    @include fluid-text(20, 18);

    color: var(--color-light);
    font-style: normal;
    font-weight: 600;
    line-height: 1.5;
    letter-spacing: -0.6px;
  }

  // .card__btn

  &__btn {
    order: 1;
    padding: fluid(18, 14) fluid(24, 20);
    @include tablet {
      order: 0;
    }
  }

  // .card__body

  &__body {
    flex-grow: 1;
    @include tablet {
      flex-grow: 0;
    }
  }

  // .card__date

  &__date {
    @include fluid-text(20, 16);

    color: var(--color-gray-60);
    display: inline-block;
    font-style: normal;
    font-weight: 600;
    line-height: 1.5;
    letter-spacing: -0.6px;
    margin-bottom: fluid(30, 20);
  }

  // .card__title

  &__title {
    @include fluid-text(26, 18);

    color: var(--color-light);
    font-style: normal;
    font-weight: 600;
    line-height: 1.5;
    letter-spacing: -0.78px;
    margin-bottom: fluid(10, 4);
  }

  // .card__text

  &__text {
    color: var(--color-gray-60);
    font-style: normal;
    font-weight: 400;
    line-height: 1.5;
    letter-spacing: -0.54px;
    margin-bottom: fluid(30, 20);
  }
}

.action-card {

  // .action-card__list

  &__list {
    display: flex;
    align-items: center;
    column-gap: 10px;
  }

  // .action-card__button

  &__button {
    padding: fluid(8, 6) fluid(16, 12);
    border-radius: 100px;
    border: 1px solid var(--color-dark-15);
    background: var(--color-dark-10);
    display: flex;
    align-items: center;
    column-gap: 4px;
    color: unset;
    svg {
      display: block;
    }
    &.is-active {
      svg {
        fill: var(--color-red);
        path {
          stroke: var(--color-red);
        }
      }
    }
  }
}

// Section blog end ========================================================================================================================================================

// Section resources start ========================================================================================================================================================

.resources {

  // .resources__subtitle

  &__subtitle {
    margin-bottom: 10px;
  }

  // .resources__title

  &__title {
    margin-bottom: fluid(16, 4);
  }

  // .resources__btn

  &__btn {
    padding: fluid(18, 14) fluid(24, 20);
  }

  // .resources__item

  &__item {
    position: relative;
    display: grid;
    grid-template-columns: 37.5% auto;
    align-items: center;
    &:not(:last-child) {
      border-bottom: var(--border);
    }
    @include tablet {
      display: block;
    }
  }

  // .resources__head

  &__head {
    padding: fluid(80, 40);
    padding-left: 0;
    @include tablet {
      padding-right: 0;
    }
  }

  // .resources__icon

  &__icon {
    @include square(80px);

    margin-bottom: fluid(50, 24);

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
  }

  // .resources__text

  &__text {
    @include fluid-text(20, 14);

    margin-bottom: fluid(50, 24);
  }

  // .resources__btn-inner

  &__btn-inner {
    padding: 18px 24px;
    width: 100%;
    margin-bottom: fluid(60, 20);
    @include tablet {
      max-width: max-content;
    }
    @include mobile-s {
      max-width: none;
      width: 100%;
    }
  }

  // .resources__team

  &__team {
    display: flex;
    column-gap: fluid(50, 30);
    align-items: center;
    background-color: var(--color-dark-10);
    border: var(--border);
    border-radius: 13px;
    max-width: max-content;
    padding: fluid(30, 30) fluid(30, 20);
    @include laptop {
      flex-direction: column;
      row-gap: 30px;
    }
    @include tablet {
      flex-direction: row;
    }
  }

  // .resources__downloaded

  &__downloaded {
    margin-bottom: 4px;
  }

  // .resources__body

  &__body {
    padding: fluid(80, 40);
    padding-right: 0;
    border-left: var(--border);
    @include tablet {
      border-left: 0;
      padding-left: 0;
    }
  }

  .team {

    // .team__list

    &__list {
      border-radius: 12px;
      padding: fluid(10, 8);
      background-color: var(--color-dark);
    }

    // .team__item

    &__item {
      --personItemSize: 50px;
    }
  }
}

.card-resources {
  display: grid;
  grid-template-columns: auto 1fr;
  row-gap: 30px;
  column-gap: 20px;
  @include mobile-m {
    grid-template-columns: 1fr;
    row-gap: 0px;
  }

  // .card-resources__field

  &__field {
    &:first-child {
      align-self: center;
    }
    &-border {
      padding: fluid(30, 20);
      background-color: var(--color-dark-10);
      border-radius: 13px;
      border: var(--border);
    }
    &-img {
      overflow: hidden;
      border-radius: 12px;
      img {
        width: 100%;
        height: fluid(332, 169);
        object-fit: cover;
        border-radius: 12px;
      }
    }
    @include mobile-m {
      &:first-child {
        margin-bottom: 10px;
      }
      &:nth-child(2), &:nth-child(3) {
        margin-bottom: 24px;
      }
      &:nth-child(4), &:nth-child(5) {
        margin-bottom: 16px;
      }
    }
  }

  // .card-resources__title

  &__title {
    max-width: 840px;
  }

  // .card-resources__field-wide

  &__field-wide {
    grid-column: -1 / 1;
  }

  // .card-resources__downloading

  &__downloading {
    display: flex;
    column-gap: 15px;
    justify-content: space-between;
    align-items: center;
  }

  // .card-resources__btn

  &__btn {
    padding: fluid(18, 14) fluid(24, 20);
  }
}

// Section resources end ========================================================================================================================================================

// Section reviews start ========================================================================================================================================================

.reviews {

  // .reviews__subtitle

  &__subtitle {
    margin-bottom: 10px;
  }

  // .reviews__btn

  &__btn {
    padding: fluid(18, 14) fluid(24, 20);
    @include mobile-s {
      width: 100%;
    }
  }

  // .reviews__wrapper

  &__wrapper {
    position: relative;
  }

  // .reviews__list

  &__list {
    --gridColumnGap: 100px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: var(--gridColumnGap);
    @include laptop {
      --gridColumnGap: 60px;
    }
    @include tablet {
      --gridColumnGap: 40px;
    }
    @include mobile {
      grid-template-columns: 1fr;
    }
  }

  // .reviews__item

  &__item {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: fluid(50, 30);
    padding-block: fluid(80, 40);
    &:not(:nth-child(3n + 3)) {
      &:after {
        content: '';
        position: absolute;
        right: calc(var(--gridColumnGap) / 2 * -1);
        top: 0;
        width: 1px;
        height: 100%;
        background-color: var(--color-dark-15);
        @include mobile {
          content: none;
        }
      }
    }
    @include mobile {
      &:not(:last-child) {
        border-bottom: var(--border);
      }
    }
  }

  // .reviews__head

  &__head {
    display: flex;
    align-items: center;
    column-gap: 12px;
  }

  // .reviews__icon

  &__icon {
    @include square(60px);

    overflow: hidden;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
    @include mobile {
      @include square(50px);
    }
  }

  // .reviews__name

  &__name {
    @include fluid-text(20, 18);

    color: var(--color-light);
    font-style: normal;
    font-weight: 500;
    line-height: 1.5;
    margin-bottom: 2px;
  }

  // .reviews__body

  &__body {
    padding: fluid(50, 30) fluid(30, 20) fluid(30, 20) fluid(30, 20);
    border-radius: 12px;
    border: var(--border);
    background-color: var(--color-dark-10);
    position: relative;
    flex-grow: 1;
  }
}

.rating {
  position: absolute;
  left: 50%;
  top: 0;
  transform: translate(-50%, -50%);
  padding: fluid(10, 8);
  border-radius: 100px;
  border: var(--border);
  background-color: var(--color-dark);
  display: inline-flex;
  column-gap: fluid(7, 4);
  flex-direction: row-reverse;
  svg {
    width: 20px;
    height: 20px;
  }
  &[data-total-value="1"] .rating__item:nth-child(n + 5) svg path,
  &[data-total-value="2"] .rating__item:nth-child(n + 4) svg path,
  &[data-total-value="3"] .rating__item:nth-child(n + 3) svg path,
  &[data-total-value="4"] .rating__item:nth-child(n + 2) svg path,
  &[data-total-value="5"] .rating__item:nth-child(n + 1) svg path{
    fill: var(--color-accent);
  }

  // .rating__item

  &__item {
    @include square(20px);
    cursor: pointer;
    transition: all 0.3s ease 0s;
    &:hover {
      svg {
        path {
          fill: var(--color-accent);
        }
      }
    }
    &:hover {
      ~ .rating__item svg path {
        fill: var(--color-accent);
      }
    }
  }
}

