      margin-bottom: fluid(50, 24);
    }

    // .content-contacts__btn

    &__btn {
      padding: fluid(18, 14) fluid(24, 20);
    }
  }

  .form {

		// .form__form

		&__form {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: fluid(50, 20);
    }

		// .form__row

		&__row {
      label {
        @include fluid-text(18, 16);

        display: block;
        color: var(--color-light);
        font-weight: 500;
        line-height: 1.5;
        margin-bottom: fluid(16, 12);
        span:not(.checkbox-custom, .checkbox-content) {
          margin-left: 5px;
          color: var(--color-red);
        }
      }

      input:not([type="checkbox"]), textarea {
        width: 100%;
        background-color: var(--color-dark-10);
        border: var(--border);
        border-radius: fluid(8, 6);
        padding: fluid(20, 16);
        box-shadow: 0px 0px 0px 4px #191919;
        &:focus-visible {
          outline: 2px dashed var(--color-accent);
          outline-offset: 4px;
          transition: none !important;
        }
      }

      input:not([type="checkbox"]), textarea, select {
        @include fluid-text(18, 14);
        font-style: normal;
        font-weight: 400;
        line-height: 1.5;
        color: var(--color-light);
      }

      select {
        border: var(--border);
        border-radius: fluid(8, 6);
        padding: fluid(20, 16) fluid(48, 48) fluid(20, 16) fluid(20, 16);
        box-shadow: 0px 0px 0px 4px #191919;
        color: var(--color-light);
        appearance: none;
        background: url('../img/icon-select.svg') 85% center/24px 24px no-repeat var(--color-dark-10);
        option {
          background-color: var(--color-dark-15);
        }
      }

      textarea {
        resize: vertical;
        min-height: fluid(163, 127);
      }

      input[type="checkbox"] {
        ~ .checkbox-content {

          @include fluid-text(18, 14);

          color: var(--color-light);
          font-weight: 400;
          line-height: 1.5;
        }
      }

      .checkbox {
        position: relative;
        display: flex;
        column-gap: 10px;
        &::selection {
          background: none;
        }
      }
      .checkbox-field {
        appearance: none;
        display: none;
      }
      .checkbox-custom {
        display: inline-block;
        cursor: pointer;
        width: fluid(28, 24);
        height: fluid(28, 24);
        border: var(--border);
        border-radius: 4px;
        position: relative;
        &:after {
          content: '';
          position: absolute;
          box-sizing: content-box;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          width: fluid(14, 10);
          height:fluid(14, 10);
          border-radius: 2px;
          background-color: var(--color-accent);
          opacity: 0;
          transition: opacity 0.3s ease 0s;
        }
      }

      .checkbox-field:checked + .checkbox-custom:after {
        opacity: 1;
      }

      .checkbox-field:disabled ~ .checkbox-content,
      .checkbox-field:disabled ~ .checkbox-custom {
        opacity: 0.4;
        pointer-events: none;
      }

      .checkbox-content {
        &::selection {
          background: none;
        }
      }

      &-select {
        display: flex;
        column-gap: fluid(16, 12);
      }

      &-policy {
        display: flex;
        justify-content: space-between;
        align-items: center;
        @include mobile-s {
          flex-direction: column;
          row-gap: 20px;
          align-items: flex-start;
        }
      }

      @include mobile-m {
        grid-column: -1 / 1;
      }
    }

		// .form__row-wide

		&__row-wide {
      grid-column: -1 / 1;
    }

		// .form__button

		&__button {
      padding: fluid(18, 14) fluid(46, 32);

      @include mobile-s {
        width: 100%;
      }
    }
  }

  .accordion-container {
    .accordion {
      background-color: var(--color-dark-10);
      border: var(--border);
      border-radius: fluid(12, 8);
      overflow: hidden;
      padding: fluid(34, 24);
      &:not(:last-child) {
        margin-bottom: fluid(30, 20);
      }
    }
    .menu-button {
      @include fluid-text(20, 16);

      color: var(--color-light);
      font-style: normal;
      font-weight: 500;
      line-height: normal;

      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      text-align: left;
      cursor: pointer;
      &.open {
        .icon {
          &:after, &:before {
            background-color: var(--color-light);
          }
          &::before {
            transform: rotate(90deg);
          }
        }
        + .content {
          height: auto;
        }
      }

    }

    .icon {
      width: fluid(24, 20);
      height: fluid(24, 20);
      display: flex;
      margin-top: -3px;
      align-items: center;
      justify-content: center;
      &:before, &:after {
        content:'';
        position: absolute;
        background-color: var(--color-accent);
        transition: ease 0.3s all;
      }
      &:before {
        width: 2px;
        height: fluid(24, 20);
      }
      &:after {
        width: fluid(24, 20);
        height: 2px;
      }
    }

    .content {
      @include fluid-text(18, 14);

      color: var(--color-gray-50);
      font-weight: 400;
      line-height: 1.5;
      background-color: var(--color-dark-10);
      transition: 0.4s;
      height: 0;
      overflow: hidden;
      p {
        margin-top: fluid(24, 16);
        padding-top: fluid(24, 16);
        border-top: var(--border);
      }
    }
  }
}

// Page contacts end ========================================================================================================================================================

// Footer start ========================================================================================================================================================

.footer {

  // .footer__columns

  &__columns {
    padding-block: fluid(80, 40);
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    column-gap: fluid(80, 20);
    row-gap: fluid(40, 20);
    @include tablet {
      grid-template-columns: repeat(4, 1fr);
    }
    @include mobile {
      grid-template-columns: repeat(2, 1fr);
    }
  }

  // .footer__column

  &__column {
    display: flex;
    flex-direction: column;
    row-gap: fluid(36, 16);
    &:last-child {
      @include tablet {
        grid-column: -1 / 1;
        .footer__list {
          display: flex;
          flex-wrap: wrap;
          column-gap: fluid(24, 12);
          row-gap: fluid(20, 6);
        }
      }
    }
  }

  // .footer__head-link

  &__head-link {
    @include fluid-text(20, 16);

    color: var(--color-light);
    font-style: normal;
    font-weight: 500;
    line-height: 1.3;
    letter-spacing: -0.6px;
  }

  // .footer__item

  &__item {
    &:not(:last-child) {
      margin-bottom: fluid(16, 8);
    }
  }

  // .footer__link

  &__link {
    @include fluid-text(18, 14);

    color: var(--color-dark-40);
    font-style: normal;
    font-weight: 400;
    line-height: 1.3;
    letter-spacing: -0.54px;
    transition: all 0.2s ease 0s;
    @include hover {
      color: var(--color-accent);
    }
    &.btn-outline {
      padding: fluid(10, 8) fluid(16, 14);
    }
  }

  // .footer__item-label

  &__item-label {
    display: flex;
    align-items: center;
    column-gap: 8px;
  }

  // .footer__label

  &__label {
    @include fluid-text(16, 12);

    color: var(--color-light);
    font-style: normal;
    font-weight: 400;
    line-height: 1.5;
    letter-spacing: -0.48px;
    padding: fluid(4, 2) fluid(12, 10);
    border-radius: 6px;
    background: linear-gradient(var(--color-dark-10), var(--color-dark-10)) padding-box, var(--gradient) border-box;
    border: rem(1) solid transparent;
  }

  // .footer__copyright

  &__copyright {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-block: fluid(40, 24);
    border-top: var(--border);
    @include mobile {
      flex-direction: column;
      row-gap: 20px;
    }
  }

  // .footer__terms

  &__terms {
    --columnGapCopyright: 22px;
    display: flex;
    column-gap: var(--columnGapCopyright);
    > li {
      a {
        @include fluid-text(18, 14);

        position: relative;
        color: var(--color-dark-40);
        font-style: normal;
        font-weight: 400;
        line-height: 1.3;
        letter-spacing: -0.54px;
        text-decoration: none;
        transition: all 0.3s ease 0s;

        @include hover {
          color: var(--color-light);
        }
      }
      &:not(:first-child) {
        a {
          &:before {
            content: '';
            width: 1px;
            height: 100%;
            background-color: #262626;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: calc(var(--columnGapCopyright) / 2 * -1);
          }
        }
      }
    }
  }

  // .footer__soc1al-list

  &__soc1al-list {
    display: flex;
    column-gap: fluid(20, 14);
    @include mobile {
      order: -1;
    }
  }

  // .footer__social-item

  &__social-item {
    @include square(24px);
  }

  // .footer__social-link

  &__social-link {
    width: 100%;
    height: 100%;
    svg {
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
      transition: all 0.3s ease 0s;
    }
    @include hover {
      svg {
        path {
          fill: var(--color-accent);
        }
      }
    }
  }

  // .footer__copy

  &__copy {
    p {
      @include fluid-text(18, 14);

      color: var(--color-dark-40);
      font-style: normal;
      font-weight: 400;
      line-height: 1.3;
      letter-spacing: -0.54px;
    }
  }
}

// Footer end ========================================================================================================================================================




