@use 'sass:math';
@use 'functions' as *;
@use "mixins" as *;

html {
  &.is-lock {
    overflow: hidden;
  }
}

.page {
  height: 100%;
  font-family: var(--font-family);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

.page__body {
  @include fluid-text(18, 14);

  margin: 0;
  min-width: 390px;
  min-height: 100%;
}

img {
  object-fit: cover;
}

.site-container {
  //overflow: hidden; // If you use position: sticky on your site, remove this setting.
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

a,
button,
label,
input,
textarea,
select,
svg * {
  transition-duration: var(--transition-duration);
}

a {
  color: inherit;

  &[class] {
    text-decoration: none;
  }
}

a.link-hover {
  @include hover {
    color: var(--color-accent);
  }
}

.visually-hidden {
  position: absolute !important;
  width: 1px !important;
  height: 1px !important;
  margin: -1px !important;
  border: 0 !important;
  padding: 0 !important;
  white-space: nowrap !important;
  clip-path: inset(100%) !important;
  clip: rect(0 0 0 0) !important;
  overflow: hidden !important;
}

body {
  background-color: var(--color-dark);
  color: var(--color-gray-60);
}

.btn-accent {
  color: var(--color-dark);
  background-color: var(--color-accent);
  font-style: normal;
  font-weight: 500;
  line-height: 1.5;
  border: 1px solid var(--color-accent);
  border-radius: 10px;
  white-space: nowrap;
  @include hover {
    background-color: var(--color-dark);
    color: var(--color-accent);
  }
}

.btn-outline {
  @include flex-center(true);

  column-gap: 10px;
  color: var(--color-gray-60);
  font-style: normal;
  font-weight: 400;
  line-height: 1.5;
  letter-spacing: -0.54px;
  border-radius: 12px;
  border: 1px solid var(--color-dark-15);
  background: var(--color-dark);
  white-space: nowrap;
  @include hover {
    border: 1px solid var(--color-accent);
    color: var(--color-accent);
  }
}

.icon-after {
  position: relative;
  padding-right: 34px;
  &:after {
    content: '';
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    width: 24px;
    height: 24px;
    background-image: url('../img/icon-arrow-top-yellow.svg');
  }
}
.h1, .h2, .h3, .h4 {
  color: var(--color-light);
  font-family: var(--font-family-accent);
  font-style: normal;
}

.h1, .h2, .h4 {
  font-weight: 500;
}

.h1 {
  @include fluid-text(70, 30);

  line-height: 1.2;
  letter-spacing: -2.1px;
}

.h2 {
  @include fluid-text(58, 28);
  line-height: 1.3;
  letter-spacing: -1.74px;
}

.h3 {
  @include fluid-text(40, 24);
  font-weight: 600;
  line-height: 1.5;
  letter-spacing: -1.2px;
}

.h4 {
  @include fluid-text(24, 18);

  font-family: var(--font-family);
  line-height: 1.5;
  letter-spacing: -0.72px;
}

.h5 {
  @include fluid-text(20, 16);

  color: var(--color-light);
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 600;
  line-height: 1.5;
  letter-spacing: -0.6px;
}

.subtitle {
  display: inline-block;
  padding: fluid(6, 4) fluid(10, 8);
  color: var(--color-light);
  font-size: fluid(20, 14);
  font-style: normal;
  font-weight: 500;
  line-height: 1.5;
  letter-spacing: -0.6px;
  border-radius: 4px;
  background: var(--color-dark-20);
}

.line-before-top {
  &:before {
    content: '';
    position: absolute;
    top: -1px;
    right: 100%;
    width: calc(((100vw + var(--container-offset)) - var(--container-width)) / 2);
    height: 1px;
    background-color: var(--color-dark-15);
  }
}

.line-before-bottom {
  &:before {
    content: '';
    position: absolute;
    top: 100%;
    right: 100%;
    width: calc(((100vw + var(--container-offset)) - var(--container-width)) / 2);
    height: 1px;
    background-color: var(--color-dark-15);
  }
}

.line-after-bottom {
  &:after {
    content: '';
    position: absolute;
    top: 100%;
    left: 100%;
    width: calc(((100vw + var(--container-offset)) - var(--container-width)) / 2);
    height: 1px;
    background-color: var(--color-dark-15);
  }
}

.line-after-top {
  &:after {
    content: '';
    position: absolute;
    bottom: 100%;
    left: 100%;
    width: calc(((100vw + var(--container-offset)) - var(--container-width)) / 2);
    height: 1px;
    background-color: var(--color-dark-15);
  }
}

.section-header {
  padding-block: fluid(120, 44);
  border-bottom: var(--border);
  border-top: var(--border);
  background-color: var(--color-dark-10);
  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 30px;
    @include mobile {
      flex-direction: column;
      row-gap: 30px;
      align-items: flex-start;
    }
  }
  &__button {
    @include mobile-s {
      width: 100%;
      button {
        width: 100%;
      }
    }
  }
}

.size-text {
  @include fluid-text(18, 14);
}

  // .reviews__divider, videos-podcasts__divider

  .reviews__divider,
  .videos-podcasts__divider {
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: var(--color-dark-15);
    @include mobile {
      display: none;
    }
  }
